    color: #fff;
    content: "\f08d";
    font-family: FontAwesome;
    font-size: 28px;
    position: absolute;
    right: 25px;
    top: 25px;
    background: #0052a5;
    width: 90px;
    height: 45px;
    text-align: center;
}


