/******------------------------------
-----------COMMENTS CSS------------
------------------------------******/
.comments-area {
    border: 1px solid #eee;
    padding: 20px 20px 30px 20px;
    box-shadow: 1px 1px 17px 1px #eee;
}

.comments-title {
    margin-bottom: 35px;
    font-size: 30px;
    color: #000000;
}

.comment-list {
    max-width: 790px;
}

.comment-list,
.children {
    list-style: outside none none;
    margin: 0;
    padding: 0;
}

.says {
    display: none;
}

.comment-author.vcard img {
    float: left;
    margin-right: 18px;
}

.comment-content {
    color: #555555;
    padding-left: 50px;
    margin-top: 15px;
    font-size: 16px;
}

pre {
    border: 1px solid #d1d1d1;
    font-size: 15px;
    line-height: 1.3125;
    margin: 30px 0 30px;
    max-width: 100%;
    overflow: auto;
    padding: 25px;
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
    background: transparent;
}

code {
    font-size: 15px;
    background: #f8f8f8;
    color: #333;
    padding: 0.125em 0.25em;
    border-radius: 0;
}

code,
kbd,
pre,
samp {
    font-size: 15px;
}

ins,
mark {
    background: #007acc;
    color: #ffffff;
    padding: 2px 5px;
    text-decoration: none;
}

kbd {
    padding: 3px 5px;
    color: #333;
    background-color: rgba(68, 68, 68, 0.2);
    border-radius: 0px;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.comment-content h1,
.comment-content h2,
.comment-content h3,
.comment-content h4,
.comment-content h5,
.comment-content h6 {
