}

.comment-list > li,
.children article {
  margin-bottom: 30px;
}

.comment-metadata a time {
  color: #777777;
  font-size: 14px;
  font-weight: 300;
}

.reply {
  padding-left: 50px;
}

.reply a {
  color: #666;
}

.children {
  padding-left: 25px;
}

.comment-form p > input,
.comment-form p textarea {
  border: 0px solid;
  padding: 11px 0 11px 10px;
  width: 100%;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  resize: none;
  background-color: #f6f6f6;
}

.comment-form textarea {
  margin-bottom: 10px;
}

.comment-form .form-submit {
  margin-top: 30px;
}

.comment-form p {
  margin-bottom: 20px;
}

.comment-form p.comment-form-comment {
