  box-shadow: none;
}

.comment-form {
  padding-right: 10px;
}

.comment-form .comment-notes {
  display: none;
}

.logged-in .form-submit {
  bottom: 4px;
  right: 0;
}

.logged-in .comment-form p:nth-child(2) {
